
.profileCoverImage {
	height: 355px;
	width: 100%;
	object-fit: cover;
}

.profileHeader {
	height: 355px;
	position: relative;
}

.profileHeader h1 {
	position: absolute;
	z-index: 4;
	color: #fff;
	font-style: normal;
	font-weight: 600;
	font-size: 30px;
	line-height: 37px;
	text-shadow: 2px 4px 40px #000000;
	display: flex;
	align-items: flex-end;	
	padding: 1rem 0 0 1rem;
}

.profileHeader ul {
	position: absolute;
	right: 0;
	bottom: 0;
	z-index: 4;
	color: #fff;
	font-style: normal;
	font-weight: 600;
	font-size: 12px;
	line-height: 20px;
	text-align: right;
	padding: 0 1rem 1rem 0;
}

.profileBody {
	padding: 1rem;
}

.profileBody h2 {
	font-weight: 600;
	font-size: 24px;
	line-height: 29px;
}