.homeContainer {
	height: 100vh;
	position: absolute;
	margin-top: -65px;
	
}

.slidesContainer {
	width: 100%;
	height: 100%;
	position: relative;
}

.slidesContainer img{
	display: none;
	object-fit: cover;
	width: 100%;
	height: 100%;
	padding: -1em 1em 1em 1em;
	opacity: 0;
	transition: opacity 0.8s ease;	
}

.homeContainer h1 {
	position: absolute;
	top: 2em;
	left: 1em;
	right: 1em;
	font-weight: 600;
	font-size: 48px;
	line-height: 60px;
	text-shadow: 2px 4px 40px #000000;
	z-index: 4;
}

.coverIndex {
	position: absolute;
	bottom: 0;
	display: flex;
	left: 50%;
	padding-bottom: 2em;
	z-index: 4;
}

.dots {
	width: 12px;
	height: 12px;
	background-color: #efefef;
	border-radius: 100%;
	margin-right: 10px;
}

 .dots.active, .dots:hover {
  opacity: 0.2;
  cursor: pointer;
}

img.active {
	display: block;
}	

@media only screen and (min-width: 1000px) { 
.homeContainer {
	margin-top: 0px;
}

.homeContainer img{
	padding: 5em 1em 1em 1em;
}

.homeContainer h1 {
	font-size: 56px;
	line-height: 68px;
}

}