
.labelsContainer header {
	display: flex;
}

.labelsContainer .listOrGrid {
	display: flex;
	padding: 1rem;
	font-size: 16px
}

.labelsContainer span {
	padding: 0 1em;
}

#listView.selected, #gridView.selected {
	border-bottom: solid thin #5E5C45;
	color: #5E5C45;
	font-size: 20px

}

.listOrGrid p, .listOrGrid span  {
	margin: auto 0;
}


.listOrGrid p:hover {
	color: #5E5C45;
	font-size: 20px
}

.spacer {
	flex: 1;
}

.listViewContainer {
	display: none;
}

.listView {
	height: 50px;
	width: 100%;
	display: flex;
	padding: 10px;
	border-top: solid #222;
}


.listView:hover {
	background-color: #222;
}

.listView:hover a {
	color: #efefef;
}

.listView a{
	width: 100%;
	display: flex;
	color: #1F2023;
	align-items: center;
}

.listView .name {
	flex-basis: 50%;
	text-align: center
}

.listView .date {
	flex-basis: 25%;
	text-align: center;
	order: 2;
}

.listView .origin {
	flex-basis: 25%;
	text-align: center;
}

.gridViewContainer {
	display: -ms-grid;
	-ms-grid-column: 1fr 1fr 1fr 1fr;
	-ms-grid-row: auto;
	display: grid;

	grid-template-columns: repeat(4, 1fr);
	grid-column-gap: 0;
	grid-row-gap: 0;
	align-items: flex-start;
	grid-auto-rows: auto;
}

.labelGrid {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.labelImage{
	border-bottom: 1px solid #000;
	border-right: 1px solid #000;
	box-sizing: border-box;
	background-position: 50%;
	position: relative;
	width: 100%;
}

.thumbnail {
	display: block;
	height: 100%;
	width: 100%;
	transition: all 1s;
}

.labelName {
	position: absolute;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	height: 100%;
	width: 100%;
	font-size: 1em;
}

.labelImage:hover {
	opacity: 0.1;
	transition: opacity 0.4s;
	filter: blur(8px);
}

.labelImage:hover .labelName {
	display: inline-flex;
	filter: brightness(200%);
	transform: scale(1.05);
	position: absolute;
}

@media only screen and (max-width: 750px) {
	.gridViewContainer {
		-ms-grid-column: (1fr)[3];
		grid-template-columns: repeat(3, 1fr);
	}

}

@media only screen and (max-width: 600px) {
	.gridViewContainer {
		-ms-grid-column: (1fr)[2];
		grid-template-columns: repeat(2, 1fr);
	}

}



@media only screen and (max-width: 400px) {
	.gridViewContainer {
		-ms-grid-column: (1fr)[1];
		grid-template-columns: repeat(1, 1fr);
	}

}

