.mob-links {
	position: relatve;
	top: 0;
	width: 100%;
}

.navBar .mob-links:hover {
	opacity: 0.5;
}

.contentSeperator {
	border-top: solid thin;
	border-bottom: solid thin;
	border-color: #5E5C45;
}

.space {
	margin: 1rem 0rem;
}

.sectionTitle {
	font-weight: 600;
	padding: 1rem 0rem 1rem 1rem;
	font-size: 30px;
}

.darkOverlay {
	height: 100%;
	width: 100%;
	position: absolute;
	background: linear-gradient(180deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0.225) 41.67%, rgba(0, 0, 0, 0.5) 85.42%);
	z-index: 3;
}

@media only screen and (min-width: 1000px) {
  .mobileNav, .latestContainer, .aboutContainer, .newsContainer, .labelsContainer, .profilesContainer, .homeContainer, .errorContainer {
   margin-left: 250px;
  }

  .latestContainer, .aboutContainer, .newsContainer, .labelsContainer, .profilesContainer, .errorContainer {
   margin-top: 70px;

  }

  .mob-links #dtn {
	position: relative;
	right: 250px;
  }

  .mob-links #menu, .mob-links #cross {
	display: none;
  }

}