
.newsThumbnail {
	height: 355px;
	width: 100%;
	position: relative;
}

.newsThumbnail h1{
	position: absolute;
	z-index: 4;
	bottom: 0;
	padding: 0 0 2rem 1rem;
	font-style: normal;
	font-weight: 600;
	font-size: 30px;
	line-height: 37px;
}

.thumbnailImage {
	height: 355px;
	width: 100%;
	object-fit: cover;
}
