.linkTemplate {
	width: 100%;
	height: 250px;
	overflow: hidden;
	position: relative;
}

.linkTemplate img {
	position: relative;
	height: 100%;
	object-fit: cover;
	width: 50%;
	left: 50%;
}

.captionBlock {
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;
	width: 60%;
	transition: transform 0.3s ease-in-out;
}

.captionBlock:hover {
	transform: translateX(-10%);

}

.captionBlock figcaption {
	position: relative;
	font-size: 36px;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	padding-left: 1rem;
}

.captionBlock time {
	position: absolute;
	font-size: 12px;
	right: 0;
	bottom: 0;
	padding: 0.5rem;
}

