.mobileNav {
	z-index: 99;
	font-size: 16px;
	position: relative;
	display: flex;	
	justify-content: space-between;
	background-color: #1F2023;
  	width: 100%;
}

.mob-links #dtn {
	margin: 2em 1em 1em 1em;
}

.mob-links #menu {
	float: left;
	margin: 2em 1em 1em 1em;
}


.caption {	
	display: none;
	position: relative;
	margin: 1.5rem 1rem 1rem 1rem;

	width: 100%;

}

.caption h4 {	
	width: 100%;
}

@media only screen and (min-width: 1000px) {

	.mob-links #dtn {
	margin: 1.5em 1em 1.5rem 1em;
	position: absolute;
	}

	.mobileNav {
  	position: fixed;
  	top: 0;
  	border-bottom: solid thin #5E5C45;
  }

  .caption {
  	display: block;
  }
}