.navBar {
	position: fixed;
	background-color: #1F2023;
	width: 100%;
	height: 100vh;
	z-index: -1;
	opacity: 0;
	top: 0;
	transition: opacity 0.1s;
	transform: translateX(-100%);
	display: flex;
	align-items: center;
	flex-direction: column;
}

.navBar.showNav {
	opacity: 1;
	z-index: 100;
	transition: opacity 0.3s;
	transform: translateX(0%);

}

.mob-links #cross {
	float: left;
	margin: 1em;
}


.navBar .logo {
	position: relative;
	margin-top: 2rem;
	width: 100%;
}

.navBar .logo img {
	width: 80%;
	max-width: 244px;
	position: relative;
	left: 50%;
	transform: translateX(-50%); 
}


.navBar .logo h1 {
	position: absolute;
	top: 50%;
	left: 50%;
	width: 100px;
	transform: translate(-50%, -50%);
	text-align: center;
	font-size: 24px
}

.navBar ul {
	display: flex;
	align-items: center;
	flex-direction: column;
	font-size: 24px;
}


.navBar ul li {
	display: block;
	padding: 2rem;
}

.navBar ul li:hover {
	opacity: 0.5;
}


@media only screen and (min-width: 1000px) {
  .navBar {
    display: block;
    width: 250px;
    opacity: 1;	
    z-index: 4;
    transform: translateX(0%);
    transition: transform 0.3s ease-in-out;
    border-right: solid thin #5E5C45;
  }

  .navBar:hover .sideBarHide {
  	display: block;
  }
}